import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\r\n  Grouped headers\r\n  <small>simple table</small>\r\n</h4>\r\n{{! BEGIN-SNIPPET table-with-grouped-headers }}\r\n<ModelsTable\r\n  @data={{this.data}}\r\n  @columns={{this.columns}}\r\n  @groupedHeaders={{this.groupedHeaders}}\r\n  @showColumnsDropdown={{false}}\r\n  @showGlobalFilter={{false}}\r\n  @useFilteringByColumns={{false}}\r\n  @themeInstance={{this.fw.themeInstance}}/>\r\n{{! END-SNIPPET }}\r\n\r\n<div class=\"emt-row\">\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-grouped-headers.hbs\" />\r\n  </div>\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-grouped-headers.js\" />\r\n  </div>\r\n</div>\r\n\r\n<h4>\r\n  Grouped headers\r\n  <small>server paginated table</small>\r\n</h4>\r\n<ModelsTableServerPaginated\r\n  @data={{this.model}}\r\n  @columns={{this.columns}}\r\n  @groupedHeaders={{this.groupedHeaders}}\r\n  @showColumnsDropdown={{false}}\r\n  @showGlobalFilter={{false}}\r\n  @useFilteringByColumns={{false}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @itemsCount={{this.itemsCount}}\r\n  @pagesCount={{this.pagesCount}}\r\n  @doQuery={{this.doQuery}}/>\r\n', { moduleName: 'dummy/templates/examples/grouped-headers.hbs' });
