import { hbs } from 'ember-cli-htmlbars';
export default hbs('<button {{on \"click\" this.doToggleGroupedRows}} type=\"button\" class=\"toggle-grouped-group\">\r\n  {{@currentGroupingPropertyName}}:\r\n  {{@groupedValue}}\r\n  ({{@visibleGroupedItems.length}}).\r\n  {{if @groupIsCollapsed \"collapsed\" \"expanded\"}}\r\n</button>\r\n<button\r\n  type=\"button\"\r\n  class=\"toggle-selection-group\"\r\n  {{on \"click\" this.doToggleGroupedRowsSelection}}>\r\n</button>\r\n<button\r\n  type=\"button\"\r\n  class=\"toggle-expands-group\"\r\n  {{on \"click\" this.doToggleGroupedRowsExpands}}>\r\n</button>\r\n<span class=\"expanded-count\">{{@expandedGroupedItems.length}}</span>\r\n<span class=\"selected-count\">{{@selectedGroupedItems.length}}</span>', { moduleName: 'dummy/templates/components/custom-row-group-toggle.hbs' });
