import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\r\n  Grouped rows\r\n  <small>simple table</small>\r\n</h4>\r\n\r\n{{#if this.fw.isBs4}}\r\n  <GroupedRowsSettingsBs4\r\n    @displayGroupedValueAsToggle={{this.displayGroupedValueAsToggle}}/>\r\n{{/if}}\r\n\r\n{{#if this.fw.isBs5}}\r\n  <GroupedRowsSettingsBs4\r\n    @displayGroupedValueAsToggle={{this.displayGroupedValueAsToggle}}/>\r\n{{/if}}\r\n\r\n{{! BEGIN-SNIPPET table-with-grouped-rows }}\r\n<ModelsTable\r\n  @data={{this.data}}\r\n  @columns={{this.columns}}\r\n  @expandedRowComponent={{component \"expanded-row\"}}\r\n  @multipleExpand={{true}}\r\n  @multipleSelect={{true}}\r\n  @useDataGrouping={{true}}\r\n  @dataGroupProperties={{this.dataGroupProperties}}\r\n  @currentGroupingPropertyName={{this.currentGroupingPropertyName}}\r\n  @displayGroupedValueAs={{this.displayGroupedValueAs}}\r\n  @collapsedGroupValues={{this.collapsedGroupValues}}\r\n  @pageSize={{this.pageSize}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @onDisplayDataChanged={{this.onDisplayDataChanged}}/>\r\n{{! END-SNIPPET }}\r\n\r\n<div class=\"emt-row\">\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-grouped-rows.hbs\"/>\r\n  </div>\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-grouped-rows.js\"/>\r\n  </div>\r\n</div>', { moduleName: 'dummy/templates/examples/grouped-rows.hbs' });
