import { hbs } from 'ember-cli-htmlbars';
export default hbs('{{! BEGIN-SNIPPET server-side-filter-component }}\r\n<PowerSelect\r\n  @allowClear={{true}}\r\n  @options={{this.instances}}\r\n  @search={{this.searchInstances}}\r\n  @searchField={{this.searchField}}\r\n  @searchEnabled={{true}}\r\n  @selected={{this.selectedInstance}}\r\n  @onChange={{this.updateColumnFilter}} as |instance|>\r\n  {{instance.firstName}} {{instance.lastName}}\r\n</PowerSelect>\r\n{{! END-SNIPPET }}\r\n{{yield}}', { moduleName: 'dummy/templates/components/server-side-filter.hbs' });
