import { hbs } from 'ember-cli-htmlbars';
export default hbs('<div>\r\n  <label for=\"custom-filter-cell-input\" class=\"emt-sr-only\">Custom filter cell input</label>\r\n  <Input\r\n    id=\"custom-filter-cell-input\"\r\n    @type=\"text\"\r\n    @value={{@column.filterString}}\r\n    class={{@themeInstance.input}}\r\n    {{on \"input\" this.updateColumnFilterString}}\r\n    placeholder={{@column.filterPlaceholder}}/>\r\n  <button\r\n    class=\"clearFilterIcon {{@themeInstance.clearFilterIcon}}\"\r\n    type=\"button\"\r\n    aria-label={{@themeInstance.clearFilterMsg}}\r\n    {{on \"click\" this.clearColumnFilterString}}>\r\n  </button>\r\n</div>\r\n{{yield}}\r\n', { moduleName: 'dummy/templates/components/filter-cell-input.hbs' });
