import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>Column sets <small>simple table</small></h4>\r\n{{! BEGIN-SNIPPET table-with-column-sets }}\r\n<ModelsTable\r\n  @data={{this.data}}\r\n  @columns={{this.columns}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @columnSets={{this.columnSets}}/>\r\n{{! END-SNIPPET }}\r\n\r\n<div class=\"emt-row\">\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-column-sets.hbs\"/>\r\n  </div>\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-column-sets.js\"/>\r\n  </div>\r\n</div>\r\n\r\n<h4>Columns sets <small>server paginated table</small></h4>\r\n<ModelsTableServerPaginated\r\n  @data={{this.model}}\r\n  @columns={{this.columns}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @columnSets={{this.columnSets}}\r\n  @doQuery={{this.doQuery}}\r\n  @itemsCount={{this.itemsCount}}\r\n  @pagesCount={{this.pagesCount}}/>', { moduleName: 'dummy/templates/examples/column-sets.hbs' });
