import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\r\n  Custom component in a cell with closure actions\r\n  <small>simple table</small>\r\n</h4>\r\n<p class=\"alert alert-info\">\r\n  Some records may be deleted from both tables in the same time.\r\n</p>\r\n{{! BEGIN-SNIPPET table-with-custom-components }}\r\n<ModelsTable\r\n  @data={{this.data}}\r\n  @columns={{this.columns}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @columnComponents={{hash\r\n    deleteRow=(\r\n      component \"delete-row-comp\"\r\n      onClick=this.deleteRecord\r\n    )\r\n  }}/>\r\n{{! END-SNIPPET }}\r\n\r\n<div class=\"emt-row\">\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-custom-components.hbs\"/>\r\n    <CodeBlockExample @snippetName=\"table-with-custom-components.js\"/>\r\n  </div>\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"delete-row-comp.hbs\"/>\r\n    <CodeBlockExample @snippetName=\"delete-row-comp.js\"/>\r\n  </div>\r\n</div>', { moduleName: 'dummy/templates/examples/custom-components-in-cell.hbs' });
