import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\r\n  In-line edit. Example of using provided cell-edit-toggle\r\n  <small>simple table</small>\r\n</h4>\r\n<p class=\"alert alert-info\">\r\n  Some records may be deleted from both tables in the same time.\r\n</p>\r\n{{! BEGIN-SNIPPET table-with-inline-edit }}\r\n<ModelsTable\r\n  @data={{this.data}}\r\n  @columns={{this.columns}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @columnComponents={{hash\r\n    editRow=(\r\n      component\r\n      (ensure-safe-component this.cellEditToggleComponent)\r\n      onSaveRow=this.onSaveRow\r\n      onCancelRow=this.onCancelRow\r\n  )\r\n  }}/>\r\n{{! END-SNIPPET }}\r\n\r\n<div class=\"emt-row\">\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-inline-edit-controller.js\"/>\r\n  </div>\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-inline-edit.hbs\"/>\r\n    <CodeBlockExample @snippetName=\"table-with-inline-edit-route.js\"/>\r\n  </div>\r\n</div>\r\n\r\n<h4>\r\n  In-line edit\r\n  <small>server paginated table</small>\r\n</h4>\r\n<ModelsTableServerPaginated\r\n  @data={{this.model}}\r\n  @columns={{this.columns}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @doQuery={{this.doQuery}}\r\n  @itemsCount={{this.itemsCount}}\r\n  @pagesCount={{this.pagesCount}}\r\n  @columnComponents={{hash\r\n    editRow=(\r\n      component\r\n      (ensure-safe-component this.cellEditToggleComponent)\r\n      onSaveRow=this.onSaveRow\r\n      onCancelRow=this.onCancelRow\r\n    )\r\n  }}/>\r\n', { moduleName: 'dummy/templates/examples/in-line-edit.hbs' });
