import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\r\n  Custom classes for columns\r\n</h4>\r\n<p class=\"alert alert-info\">\r\n  Setting the <code>className</code> property on a column adds a css class to each cell in a column. This can be useful for styling columns.\r\n</p>\r\n{{! BEGIN-SNIPPET table-with-custom-column-classes }}\r\n<ModelsTable\r\n  @data={{this.data}}\r\n  @columns={{this.columns}}\r\n  @themeInstance={{this.fw.themeInstance}}/>\r\n{{! END-SNIPPET }}\r\n<div class=\"emt-row\">\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-custom-column-classes.hbs\" />\r\n    <CodeBlockExample @snippetName=\"table-with-custom-column-classes.scss\" />\r\n  </div>\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-custom-column-classes.js\" />\r\n  </div>\r\n</div>', { moduleName: 'dummy/templates/examples/custom-column-classes.hbs' });
