import { hbs } from 'ember-cli-htmlbars';
export default hbs('<tr>\r\n  {{#each @visibleProcessedColumns as |column index|}}\r\n    <td>\r\n      Column #{{index}}.\r\n      <span class=\"expanded\">{{@expandedGroupedItems.length}}</span>\r\n      <span class=\"visible\">{{@visibleGroupedItems.length}}</span>\r\n      <span class=\"selected\">{{@selectedGroupedItems.length}}</span>\r\n    </td>\r\n  {{/each}}\r\n</tr>', { moduleName: 'dummy/templates/components/custom-group-summary-row.hbs' });
