import { hbs } from 'ember-cli-htmlbars';
export default hbs('{{! BEGIN-SNIPPET nested-table-component }}\r\n<div class=\"emt-row\">\r\n  <div class=\"emt-col\">\r\n    Nested table with comments belong to user\r\n    <strong>{{this.record.firstName}} {{this.record.lastName}}</strong>\r\n  </div>\r\n  <div class=\"emt-col\">\r\n    <ModelsTable\r\n      @data={{@record.comments}}\r\n      @columns={{this.columns}}\r\n      @multipleSelect={{true}}\r\n      @isolated={{true}}\r\n      @themeInstance={{this.fw.themeInstance}}\r\n      @columnComponents={{hash\r\n        editRow=(\r\n          component\r\n          (ensure-safe-component this.cellEditToggleComponent)\r\n          onSaveRow=this.onSaveRow\r\n          onCancelRow=this.onCancelRow\r\n        )\r\n      }}\r\n    />\r\n  </div>\r\n</div>\r\n{{! END-SNIPPET }}\r\n{{yield}}', { moduleName: 'dummy/templates/components/nested-table.hbs' });
