import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\r\n  Sort and filter by custom property\r\n  <small>simple table</small>\r\n</h4>\r\n<p class=\"alert alert-info\">\r\n  Filtering and sorting for \"First name\" column is done by <code>lastName</code>-property. Filtering and sorting for \"Last name\" column is done by <code>firstName</code>-property.\r\n</p>\r\n<p class=\"alert alert-info\">\r\n  Sorting on \"Index\" column is done by a custom sort function\r\n</p>\r\n{{! BEGIN-SNIPPET table-with-custom-sorting-and-filtering }}\r\n<ModelsTable\r\n  @data={{this.data}}\r\n  @columns={{this.columns}}\r\n  @multipleColumnsSorting={{false}}\r\n  @themeInstance={{this.fw.themeInstance}}/>\r\n{{! END-SNIPPET }}\r\n\r\n<div class=\"emt-row\">\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-custom-sorting-and-filtering.hbs\"/>\r\n  </div>\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-custom-sorting-and-filtering.js\"/>\r\n  </div>\r\n</div>\r\n\r\n<h4>\r\n  Sort and filter by custom property\r\n  <small>server paginated table</small>\r\n</h4>\r\n<p class=\"alert alert-info\">\r\n  Filtering and sorting for \"First name\" column is done by <code>lastName</code>-property. Filtering and sorting for \"Last name\" column is done by <code>firstName</code>-property.\r\n</p>\r\n<ModelsTableServerPaginated\r\n  @data={{this.model}}\r\n  @columns={{this.columns}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @doQuery={{this.doQuery}}\r\n  @itemsCount={{this.itemsCount}}\r\n  @pagesCount={{this.pagesCount}}/>\r\n', { moduleName: 'dummy/templates/examples/sort-by-filter-by.hbs' });
