import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\r\n  Common table\r\n  <small>simple table</small>\r\n</h4>\r\n\r\n<p>This options are applied for both tables:</p>\r\n{{component\r\n  (ensure-safe-component this.settingsComponent)\r\n  showComponentFooter=this.showComponentFooter\r\n  showColumnsDropdown=this.showColumnsDropdown\r\n  useFilteringByColumns=this.useFilteringByColumns\r\n  showGlobalFilter=this.showGlobalFilter\r\n  useNumericPagination=this.useNumericPagination\r\n  doFilteringByHiddenColumns=this.doFilteringByHiddenColumns\r\n  filteringIgnoreCase=this.filteringIgnoreCase\r\n  multipleColumnsSorting=this.multipleColumnsSorting\r\n  showPageSize=this.showPageSize\r\n  collapseNumPaginationForPagesCount=this.collapseNumPaginationForPagesCount\r\n  showCurrentPageNumberSelect=this.showCurrentPageNumberSelect\r\n}}\r\n\r\n{{! BEGIN-SNIPPET common-table }}\r\n<ModelsTable\r\n  @data={{this.data}}\r\n  @columns={{this.columns}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @showComponentFooter={{this.showComponentFooter}}\r\n  @showColumnsDropdown={{this.showColumnsDropdown}}\r\n  @useFilteringByColumns={{this.useFilteringByColumns}}\r\n  @showGlobalFilter={{this.showGlobalFilter}}\r\n  @doFilteringByHiddenColumns={{this.doFilteringByHiddenColumns}}\r\n  @useNumericPagination={{this.useNumericPagination}}\r\n  @filteringIgnoreCase={{this.filteringIgnoreCase}}\r\n  @multipleColumnsSorting={{this.multipleColumnsSorting}}\r\n  @showCurrentPageNumberSelect={{this.showCurrentPageNumberSelect}}\r\n  @collapseNumPaginationForPagesCount={{this.collapseNumPaginationForPagesCount}}\r\n  @currentPageNumber={{this.currentPageNumber}}\r\n  @showPageSize={{this.showPageSize}}\r\n  @onDisplayDataChanged={{this.onDisplayDataChanged}}/>\r\n{{! END-SNIPPET }}\r\n\r\n<CodeBlockExample @snippetName=\"common-table.hbs\"/>\r\n\r\n<h4>\r\n  Common table\r\n  <small>server paginated table</small>\r\n</h4>\r\n{{! BEGIN-SNIPPET common-table-server-paginated }}\r\n<ModelsTableServerPaginated\r\n  @data={{this.model}}\r\n  @columns={{this.columns}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @filterQueryParameters={{this.filterQueryParameters}}\r\n  @showComponentFooter={{this.showComponentFooter}}\r\n  @showColumnsDropdown={{this.showColumnsDropdown}}\r\n  @useFilteringByColumns={{this.useFilteringByColumns}}\r\n  @showGlobalFilter={{this.showGlobalFilter}}\r\n  @useNumericPagination={{this.useNumericPagination}}\r\n  @showCurrentPageNumberSelect={{this.showCurrentPageNumberSelect}}\r\n  @collapseNumPaginationForPagesCount={{this.collapseNumPaginationForPagesCount}}\r\n  @showPageSize={{this.showPageSize}}\r\n  @onDisplayDataChanged={{this.onDisplayDataChanged}}\r\n  @doQuery={{this.doQuery}}\r\n  @itemsCount={{this.itemsCount}}\r\n  @pagesCount={{this.pagesCount}}/>\r\n{{! END-SNIPPET }}\r\n<CodeBlockExample @snippetName=\"common-table-server-paginated.hbs\"/>', { moduleName: 'dummy/templates/examples/common-table.hbs' });
