import { hbs } from 'ember-cli-htmlbars';
export default hbs('<p>\r\n  <label>\r\n    <Input\r\n      @type=\"checkbox\"\r\n      name=\"showComponentFooter\"\r\n      @checked={{this.showComponentFooter}}/> Show Footer (<code>showComponentFooter</code>)\r\n  </label>\r\n</p>\r\n<p>\r\n  <label>\r\n    <Input\r\n      @type=\"checkbox\"\r\n      name=\"showColumnsDropdown\"\r\n      @checked={{this.showColumnsDropdown}}/> Show Columns Dropdown (<code>showColumnsDropdown</code>)\r\n  </label>\r\n</p>\r\n<p>\r\n  <label>\r\n    <Input\r\n      @type=\"checkbox\"\r\n      name=\"useFilteringByColumns\"\r\n      @checked={{this.useFilteringByColumns}}/> Use filtering by columns (<code>useFilteringByColumns</code>)\r\n  </label>\r\n</p>\r\n<p>\r\n  <label>\r\n    <Input\r\n      @type=\"checkbox\"\r\n      name=\"showGlobalFilter\"\r\n      @checked={{this.showGlobalFilter}}/> Show global filter (<code>showGlobalFilter</code>)\r\n  </label>\r\n</p>\r\n<p>\r\n  <label>\r\n    <Input\r\n      @type=\"checkbox\"\r\n      name=\"useNumericPagination\"\r\n      @checked={{this.useNumericPagination}}/> Use numeric pagination (<code>useNumericPagination</code>)\r\n  </label>\r\n</p>\r\n<p>\r\n  <label>\r\n    <Input\r\n      @type=\"checkbox\"\r\n      name=\"showPageSize\"\r\n      @checked={{this.showPageSize}}/> Show page size select-box (<code>showPageSize</code>)\r\n  </label>\r\n</p>\r\n<p>Used only for first table:</p>\r\n<p>\r\n  <label>\r\n    <Input\r\n      @type=\"checkbox\"\r\n      name=\"filteringIgnoreCase\"\r\n      @checked={{this.filteringIgnoreCase}}/> Do filtering ignore case (<code>filteringIgnoreCase</code>)\r\n  </label>\r\n</p>\r\n<p>\r\n  <label>\r\n    <Input\r\n      @type=\"checkbox\"\r\n      name=\"multipleColumnsSorting\"\r\n      @checked={{this.multipleColumnsSorting}}/> Do sorting by multiple columns (<code>multipleColumnsSorting</code>)\r\n  </label>\r\n</p>\r\n<p>\r\n  <label>\r\n    <Input\r\n      @type=\"checkbox\"\r\n      name=\"doFilteringByHiddenColumns\"\r\n      @checked={{this.doFilteringByHiddenColumns}}/> Do filtering by hidden columns\r\n    (<code>doFilteringByHiddenColumns</code>)\r\n  </label>\r\n</p>\r\n<p>\r\n  <label>\r\n    <Input\r\n      @type=\"checkbox\"\r\n      name=\"showCurrentPageNumberSelect\"\r\n      @checked={{this.showCurrentPageNumberSelect}}/> Show dropdown for current page number (<code>showCurrentPageNumberSelect</code>)\r\n  </label>\r\n</p>\r\n<p class=\"form-inline\">\r\n  <label>\r\n    <Input\r\n      class=\"form-control\"\r\n      name=\"collapseNumPaginationForPagesCount\"\r\n      @value={{this.collapseNumPaginationForPagesCount}}/>\r\n    Collapse numeric pagination if pages count is greater (<code>collapseNumPaginationForPagesCount</code>)\r\n  </label>\r\n</p>\r\n{{yield}}', { moduleName: 'dummy/templates/components/common-table-settings-bs4.hbs' });
