import { hbs } from 'ember-cli-htmlbars';
export default hbs('<div class=\"code-block-example\">\r\n  {{#let (get-code-snippet @snippetName) as |snippet|}}\r\n    <h4>{{@snippetName}}</h4>\r\n    <button\r\n      onclick={{this.toggleCodeVisibility}}\r\n      type=\"button\"\r\n      class={{this.fw.themeInstance.buttonDefault}}>\r\n      {{#if this.opened}}Hide{{else}}Show{{/if}} code\r\n    </button>\r\n    {{#if this.opened}}\r\n      <CodeBlock\r\n        class=\"code-block\"\r\n        @code={{snippet.source}}\r\n        @language={{snippet.language}}/>\r\n    {{/if}}\r\n  {{/let}}\r\n  {{yield}}\r\n</div>', { moduleName: 'dummy/templates/components/code-block-example.hbs' });
