import { hbs } from 'ember-cli-htmlbars';
export default hbs('<p class=\"alert alert-info\">Check console to get data sent to the parent context</p>\r\n<h4>\r\n  Handle user interaction with a component\r\n  <small>simple table</small>\r\n</h4>\r\n{{! BEGIN-SNIPPET display-data-changed-action }}\r\n<ModelsTable\r\n  @data={{this.data}}\r\n  @columns={{this.columns}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @onDisplayDataChanged={{this.myAction}}/>\r\n{{! END-SNIPPET }}\r\n\r\n<div class=\"emt-row\">\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"display-data-changed-action.hbs\"/>\r\n  </div>\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"display-data-changed-action.js\"/>\r\n  </div>\r\n</div>\r\n\r\n<h4>\r\n  Handle user interaction with a component\r\n  <small>server paginated table</small>\r\n</h4>\r\n<ModelsTableServerPaginated\r\n  @data={{this.model}}\r\n  @columns={{this.columns}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @itemsCount={{this.itemsCount}}\r\n  @pagesCount={{this.pagesCount}}\r\n  @doQuery={{this.doQuery}}\r\n  @onDisplayDataChanged={{this.myAction}}/>\r\n', { moduleName: 'dummy/templates/examples/display-data-changed-action.hbs' });
