import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\r\n  Expandable Rows\r\n  <small>simple table</small>\r\n</h4>\r\n<p class=\"alert alert-info\">\r\n  Some column should has component with actions <code>expandRow</code> and <code>collapseRow</code>. This column should have field <code>mayBeHidden</code> set to <code>false</code>. Value for <code>expandedRowComponent</code> should be provided to the component. It should be equal to the component name shown in the expanded row. Set <code>multipleExpand</code> to <code>true</code> to allow multiple rows be expanded in the same time.\r\n</p>\r\n{{! BEGIN-SNIPPET table-with-expandable-rows }}\r\n<ModelsTable\r\n  @data={{this.data}}\r\n  @columns={{this.columns}}\r\n  @expandedRowComponent={{component \"expanded-row\"}}\r\n  @multipleExpand={{true}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @expandedItems={{this.expandedItems}}/>\r\n{{! END-SNIPPET }}\r\n\r\n<div class=\"emt-row\">\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-expandable-rows.hbs\"/>\r\n    <CodeBlockExample @snippetName=\"table-with-expandable-rows.js\"/>\r\n  </div>\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"expanded-row.hbs\"/>\r\n    <CodeBlockExample @snippetName=\"expanded-row.js\"/>\r\n  </div>\r\n</div>', { moduleName: 'dummy/templates/examples/expandable-rows.hbs' });
