import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\r\n  Route cells\r\n  <small>simple table</small>\r\n</h4>\r\n<p class=\"alert alert-info\">\r\n  firstName cell is a link to the route for model in the row.\r\n</p>\r\n{{! BEGIN-SNIPPET table-with-route-cells }}\r\n<ModelsTable\r\n  @data={{this.data}}\r\n  @columns={{this.columns}}\r\n  @themeInstance={{this.fw.themeInstance}}/>\r\n{{! END-SNIPPET }}\r\n\r\n<div class=\"emt-row\">\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-route-cells.hbs\"/>\r\n  </div>\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"table-with-route-cells.js\"/>\r\n  </div>\r\n</div>\r\n\r\n<h4>\r\n  Route cells\r\n  <small>server paginated table</small>\r\n</h4>\r\n<ModelsTableServerPaginated\r\n  @data={{this.model}}\r\n  @columns={{this.columns}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n  @itemsCount={{this.itemsCount}}\r\n  @pagesCount={{this.pagesCount}}\r\n  @doQuery={{this.doQuery}}/>\r\n', { moduleName: 'dummy/templates/examples/route-cells.hbs' });
