import { hbs } from 'ember-cli-htmlbars';
export default hbs('<h4>\r\n  Nested table\r\n  <small>simple table</small>\r\n</h4>\r\n\r\n{{! BEGIN-SNIPPET nested-table }}\r\n<ModelsTable\r\n  @data={{this.data}}\r\n  @columns={{this.columns}}\r\n  @multipleExpand={{true}}\r\n  @expandedRowComponent={{component \"nested-table\"}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\n/>\r\n{{! END-SNIPPET }}\r\n\r\n<div class=\"emt-row\">\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"nested-table.js\"/>\r\n    <CodeBlockExample @snippetName=\"nested-table.hbs\"/>\r\n  </div>\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"nested-table-component.js\"/>\r\n    <CodeBlockExample @snippetName=\"nested-table-component.hbs\"/>\r\n  </div>\r\n</div>\r\n\r\n<h4>\r\n  Nested table\r\n  <small>block usage</small>\r\n</h4>\r\n\r\n{{! BEGIN-SNIPPET nested-table-block }}\r\n<ModelsTable\r\n  @data={{this.data}}\r\n  @columns={{this.columns}}\r\n  @multipleExpand={{true}}\r\n  @themeInstance={{this.fw.themeInstance}}\r\nas |MT|>\r\n  <div class={{MT.themeInstance.headerWrapper}}>\r\n    <MT.GlobalFilter />\r\n    <MT.ColumnsDropdown />\r\n  </div>\r\n  <MT.Table as |Table|>\r\n    <Table.Header/>\r\n    <Table.Body as |Body|>\r\n      {{#if MT.allColumnsAreHidden}}\r\n        <Body.ColumnsHidden/>\r\n      {{else}}\r\n        {{#each MT.visibleContent as |record index|}}\r\n          <Body.Row\r\n            @record={{record}}\r\n            @index={{index}}/>\r\n          {{#if (includes record MT.expandedItems)}}\r\n            <Body.RowExpand\r\n              @record={{record}}\r\n              @index={{index}}>\r\n              <div class=\"emt-row\">\r\n                <div class=\"emt-col\">\r\n                  Nested table with comments belong to user\r\n                  <strong>{{this.record.firstName}} {{this.record.lastName}}</strong>\r\n                </div>\r\n                <div class=\"emt-col\">\r\n                  <ModelsTable\r\n                    @data={{record.comments}}\r\n                    @columns={{this.nestedColumns}}\r\n                    @multipleSelect={{true}}\r\n                    @isolated={{true}}\r\n                    @themeInstance={{this.fw.themeInstance}}\r\n                    @columnComponents={{hash\r\n                      editRow=(\r\n                        component\r\n                        (ensure-safe-component this.cellEditToggleComponent)\r\n                        onSaveRow=this.onSaveRow\r\n                        onCancelRow=this.onCancelRow\r\n                      )\r\n                    }}\r\n                  />\r\n                </div>\r\n              </div>\r\n            </Body.RowExpand>\r\n          {{/if}}\r\n        {{else}}\r\n          <Body.NoData/>\r\n        {{/each}}\r\n      {{/if}}\r\n    </Table.Body>\r\n  </MT.Table>\r\n  <MT.Footer/>\r\n</ModelsTable>\r\n{{! END-SNIPPET }}\r\n<div class=\"emt-row\">\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"nested-table-block.hbs\"/>\r\n  </div>\r\n  <div class=\"emt-col\">\r\n    <CodeBlockExample @snippetName=\"nested-table-controller.js\"/>\r\n  </div>\r\n</div>\r\n{{outlet}}', { moduleName: 'dummy/templates/examples/nested-table.hbs' });
